import { Component, OnInit, ViewChild } from "@angular/core";
import { Poliza } from "../../classes/vehiculo/polizas";
import { Soat } from "../../classes/vehiculo/soat";
import { Tecnomecanica } from "src/app/classes/vehiculo/tecnomecanica";
import { LicenciaTransito } from "src/app/classes/vehiculo/licenciaT";
import { PersonaNaturar } from "src/app/classes/vehiculo/persona_naturar";
import { Empresa } from "src/app/classes/vehiculo/empresa";
import { Vehiculo } from "src/app/classes/vehiculo/vehiculo";
import { VehiculoService } from "src/app/services/vehiculo.service";
import {
  MatSnackBar,
  MatPaginator,
  MatDialog,
  MatSort,
  MatTableDataSource,
  MatCheckboxChange
} from "@angular/material";
import { finalize } from "rxjs/operators";
import { DetallesComponent } from './detalles/detalles.component';
@Component({
  selector: "app-vehiculo",
  templateUrl: "./vehiculo.component.html",
  styleUrls: ["./vehiculo.component.css"]
})
export class VehiculoComponent implements OnInit {
  number:number=10;
  uploadbar: boolean;
  validodisable: boolean;
  constructor(
    public vehiculoservice: VehiculoService,
    public snack: MatSnackBar,
    public dialog: MatDialog,
   
  ) {}
  displayedColumns: string[] = [
    "datos",
    "licencia",
    "tecnomecanica",
    "soat",
    "poliza",
    "editar",
    "eliminar",
    "detalles",
    "validar"
    
  ];
  dataSource = new MatTableDataSource();
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  ngOnInit() {
    this.uploadbar = true;
    this.paginator._intl.itemsPerPageLabel = "Item por pagina";
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.vehiculoservice
      .get()
      .pipe(finalize(() => (this.uploadbar = false)))
      .subscribe((data: Vehiculo[]) => {
        this.dataSource.data = data;
        this.validodisable=false;
      });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  delete(id) {
    let comfirm = confirm("Esta seguro de Borrar el Vehiculo");
    if (comfirm)
      this.vehiculoservice.delete(id).subscribe((data: any) => {
        console.log(data);
        if (!data.error) {
          this.snack.open("Vehiculo Elimado", "Aceptar", {
            duration: 3000
          });
          this.ngOnInit();
        }
      });
    }
    viewdetalles(vehiculo:Vehiculo) {
      const dialogref = this.dialog.open(DetallesComponent, {
        width: "1000px",
        data: vehiculo
      });
    }
    validar(event,vehiculo){
      this.validodisable=true;
      let valido = {
        _id: vehiculo,
        valid: event.checked
      };
      this.vehiculoservice.validar(valido).subscribe((data: any) => {
        
        if (!data.error) {
          this.snack.open("Vehiculo Validado", "Aceptar", {
            duration: 3000
          });
         this.ngOnInit()
        } else {
          this.snack.open("Error al Validar", "Aceptar", {
            duration: 3000
          });
        }
      });

    }
}
