import { Pipe, PipeTransform } from "@angular/core";
import { Moment } from "moment";
import * as moment from "moment";

@Pipe({
  name: "caducidad"
})
export class CaducidadPipe implements PipeTransform {
  transform(value: any, args?: number): any {
    if(value!=null){
      let now = moment();
      let fecha = moment(value);
      let fechastring = moment(value)
        .locale("es")
        .format("LL");
      let diferenciadias = fecha.diff(now, "days");
      let diferenciahoras = fecha.diff(now, "hours");
      if (diferenciadias < args) {
        if (diferenciadias <= 0)
          return diferenciahoras <= 0
            ? "Vencido"
            : `${fechastring} (Faltan ${diferenciahoras} Horas )`;
        return diferenciadias >= 2
          ? `${fechastring} (Faltan ${diferenciadias} Dias)`
          : `${fechastring} (Falta ${diferenciadias} Dia y ${diferenciahoras} Horas)`;
      } else return fechastring;

    }else  return "Sin fecha"
    
  }
}
