import { Component, OnInit, Input } from "@angular/core";
import { Trailer } from "src/app/classes/trailes/trailer";
import { LicenciaTransito } from "../../classes/vehiculo/licenciaT";
import { KingPing } from "../../classes/trailes/kingping";
import { FormGroup } from "@angular/forms";
import { TrailersService } from "src/app/services/trailers.service";
import { finalize } from "rxjs/operators";
import { MatSnackBar } from "@angular/material";
import { Router } from "@angular/router";

@Component({
  selector: "app-addtanque",
  templateUrl: "./addtanque.component.html",
  styleUrls: ["./addtanque.component.css"]
})
export class AddtanqueComponent implements OnInit {
  loading: boolean;
  @Input() update: boolean;
  @Input() placaupdate: string;
  formulario = new FormData();
  licenciaT = new LicenciaTransito();
  kingping = new KingPing();
  tanque = new Trailer(this.licenciaT, this.kingping);

  constructor(
    public trailerservice: TrailersService,
    public snackBar: MatSnackBar,
    public nav: Router
  ) {
    this.tanque.tipo = "tanque";
  }

  ngOnInit() {
    console.log(this.placaupdate, this.update);

    if (this.update) {
      this.trailerservice
        .gettrailer(this.placaupdate)
        .subscribe((data: any) => {
          this.tanque = data;
        });
    }
  }

  upload(form: FormGroup) {
    this.formulario.append("placa", this.tanque.placa);
    this.formulario.append("tipo", this.tanque.tipo);
    if (form.valid) {
      if (this.update) {
        this.trailerservice
          .updatefiles(this.formulario)
          .pipe(
            finalize(() =>
              this.trailerservice
                .update(this.tanque)
                .subscribe((data: any) => {
                  if (!data.error) {
                    this.snackBar.open("Tanque Guardado", "Aceptar", {
                      duration: 3000
                    });
                    this.nav
                      this.nav.navigate(["/Home/trailer"]);
                  } else
                    this.snackBar.open("la Placa Ya existe", "Aceptar", {
                      duration: 3000
                    });
          
                })
            )
          )
          .subscribe();
      } else {
        this.loading = true;
        this.trailerservice
          .upload(this.formulario)
          .pipe(
            finalize(() => {
              this.trailerservice
                .addtrailer(this.tanque)
                .subscribe((data: any) => {
                  console.log(data);

                  if (!data.error) {
                    this.snackBar.open("Tanque Guardado", "Aceptar", {
                      duration: 3000
                    });
                    this.nav
                      .navigateByUrl("", { skipLocationChange: true })
                      .then(() => this.nav.navigate(["/Home/addtanque"]));
                  } else
                    this.snackBar.open("la Placa Ya existe", "Aceptar", {
                      duration: 3000
                    });
                });
            })
          )
          .subscribe();
      }
    } else {
    }
    console.log(this.tanque);
  }
  getfiles(event, tipo: string) {
    if (tipo == "licencia") {
      this.formulario.append("licencia", <File>event.files[0], "licencia.pdf");
      this.tanque.licencia.url = `http://localhost:3000/Documentos/Trailers/${
        this.tanque.tipo
      }-${this.tanque.placa}/licencia.pdf`;
    }
    if (tipo == "foto") {
      this.formulario.append("foto", <File>event.files[0], "foto.pdf");
      this.tanque.foto = `http://localhost:3000/Documentos/Trailers/${
        this.tanque.tipo
      }-${this.tanque.placa}/foto.pdf`;
    }
    if (tipo == "hidrostatica") {
      this.formulario.append(
        "hidrostatica",
        <File>event.files[0],
        "hidrostatica.pdf"
      );
      this.tanque.urlhidrostatica = `http://localhost:3000/Documentos/Trailers/${
        this.tanque.tipo
      }-${this.tanque.placa}/hidrostatica.pdf`;
    }
    if (tipo == "aforos") {
      this.formulario.append("aforos", <File>event.files[0], "aforos.pdf");
      this.tanque.urlaforos = `http://localhost:3000/Documentos/Trailers/${
        this.tanque.tipo
      }-${this.tanque.placa}/aforos.pdf`;
    }
    if (tipo == "lineas") {
      this.formulario.append("lineas", <File>event.files[0], "lineas.pdf");
      this.tanque.urllineas = `http://localhost:3000/Documentos/Trailers/${
        this.tanque.tipo
      }-${this.tanque.placa}/lineas.pdf`;
    }
    if (tipo == "kingping") {
      this.formulario.append("kingping", <File>event.files[0], "kingping.pdf");
      this.tanque.kinping.url = `http://localhost:3000/Documentos/Trailers/${
        this.tanque.tipo
      }-${this.tanque.placa}/kingping.pdf`;
    }
  }
}
