import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { LoginComponent } from "./componets/login/login.component";
import { HomeComponent } from "./componets/home/home.component";
import { LoginService } from "./services/login.service";
import { AddcontratoComponent } from "./componets/addcontrato/addcontrato.component";
import { AddvehiculoComponent } from './componets/addvehiculo/addvehiculo.component';
import { AddconductorComponent } from './componets/addconductor/addconductor.component';
import { TrailerComponent } from './componets/trailer/trailer.component';
import { UsuarioComponent } from './componets/usuario/usuario.component';
import { ConductoresComponent } from './componets/conductores/conductores.component';
import { UpdateconductorComponent } from './componets/updateconductor/updateconductor.component';
import { VehiculoComponent } from './componets/vehiculo/vehiculo.component';
import { UpdatevehiculoComponent } from './componets/updatevehiculo/updatevehiculo.component';
import { AddtanqueComponent } from './componets/addtanque/addtanque.component';
import { AddcamabajaComponent } from './componets/addcamabaja/addcamabaja.component';
import { AddcamaaltaComponent } from './componets/addcamaalta/addcamaalta.component';
import { UpdatetanqueComponent } from './componets/updatetrailer/updatetanque.component';

const routes: Routes = [
  { path: "login", component: LoginComponent },
  {
    path: "Home",
    component: HomeComponent,
    //canActivate: [LoginService],
    children: [
      {
        path: "addcontrato",
        component: AddcontratoComponent
      },
      {
        path:"addvehiculo",
        component:AddvehiculoComponent
      },
      {
        path:"addconductor",
        component:AddconductorComponent
      },
      {
        path:"addtanque",
        component:AddtanqueComponent
      },
      {
        path:"addcamabaja",
        component:AddcamabajaComponent
      },
      {
        path:"addcamaalta",
        component:AddcamaaltaComponent
      },
      {
        path:"conductor/:cedula",
        component:UpdateconductorComponent
      },
      {
        path:"vehiculo/:placa",
        component:UpdatevehiculoComponent
      },
      {
        path:"conductor",
        component:ConductoresComponent
      },
      {
        path:"trailer",
        component:TrailerComponent
      },
      {
        path:"tanque/:placa",
        component:UpdatetanqueComponent
      },
      {
        path:"usuarios",
        component:UsuarioComponent
      },
      {
        path:"vehiculo",
        component:VehiculoComponent
      }
    ]
  },
  { path: "**", component: LoginComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{onSameUrlNavigation: 'reload',useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule {}
