import { Component, OnInit, Input } from "@angular/core";
import { Vehiculo } from "../../classes/vehiculo/vehiculo";
import { LicenciaTransito } from "src/app/classes/vehiculo/licenciaT";
import { Poliza } from "../../classes/vehiculo/polizas";
import { Soat } from "../../classes/vehiculo/soat";
import { Tecnomecanica } from "src/app/classes/vehiculo/tecnomecanica";
import { PersonaNaturar } from "../../classes/vehiculo/persona_naturar";
import { Empresa } from "../../classes/vehiculo/empresa";
import { MatTabChangeEvent, MatSnackBar, MatDialog } from "@angular/material";
import { VehiculoService } from "src/app/services/vehiculo.service";
import { finalize } from "rxjs/operators";
import { Router } from "@angular/router";

@Component({
  selector: "app-addvehiculo",
  templateUrl: "./addvehiculo.component.html",
  styleUrls: ["./addvehiculo.component.css"]
})
export class AddvehiculoComponent implements OnInit {
  @Input() vehiculoplaca: string;
  @Input() update: boolean = false;
  tabselec: number = 0;
  tabdisabledp: boolean;
  tabdisablede: boolean;
  loading: boolean;
  clasedepropetario: any[] = [
    { value: "Dueño de Empresa", viewValue: "Dueño de Empresa" },
    { value: "Persona Natural", viewValue: "Persona Natural" }
  ];
  formulario = new FormData();
  polizas: Poliza[] = [
    new Poliza("Poliza responsabilidad civil"),
    new Poliza("Poliza Hidrocarburos")
  ];
  soat = new Soat();
  tecnomecanica = new Tecnomecanica();
  licencia = new LicenciaTransito();
  persona_naturar = new PersonaNaturar();
  empresa = new Empresa();
  vehiculo = new Vehiculo(
    this.licencia,
    this.polizas,
    this.soat,
    this.tecnomecanica,
    this.persona_naturar,
    this.empresa
  );

  constructor(
    public vehiculoservice: VehiculoService,
    public snack: MatSnackBar,
    public nav: Router
  ) {}

  ngOnInit() {
    this.loading = false;
    console.log(this.update, this.vehiculoplaca);
    if (this.update) {
      this.vehiculoservice
        .getvehiculo(this.vehiculoplaca)
        .subscribe((data: Vehiculo) => {
          console.log(data);

          this.vehiculo = data;
          for (const key in this.vehiculo.empresa) {
            let c = 0;
            c++;
            if (c > 0) {
              this.tabdisabledp = true;
              this.tabselec = 1;
            }
          }
          for (const key in this.vehiculo.persona_naturar) {
            let c = 0;
            c++;
            if (c > 0) {
              this.tabdisablede = true;
              this.tabselec=0
            }
          }
        });
    }
  }
  upload(form) {
    console.log(this.vehiculo);
    this.formulario.append("placa", this.vehiculo.placa);
    if (form.valid)
      if (this.update) {
        this.vehiculoservice
          .updatefiles(this.formulario)
          .pipe(
            finalize(() =>
              this.vehiculoservice
                .update(this.vehiculo)
                .subscribe((data: any) => {
                  if (!data.error) {
                    this.snack.open("Vehiculo Actulizado", "Aceptar", {
                      duration: 3000
                    });
                  
                    this.nav.navigate(["/Home/vehiculo"]);
                  }
                })
            )
          )
          .subscribe();
      } else {
        this.loading = true;
        this.vehiculoservice
          .uploadfiles(this.formulario)
          .pipe(
            finalize(() => {
              this.vehiculoservice
                .addvehiculo(this.vehiculo)
                .subscribe((data: any) => {
                  console.log(data);

                  if (data.status) {
                    this.snack.open("Vehiculo Agregado", "Aceptar", {
                      duration: 3000
                    });
                    this.nav
                      .navigateByUrl("", { skipLocationChange: true })
                      .then(() => this.nav.navigate(["/Home/addvehiculo"]));
                  }
                  if (data.error) {
                    this.snack.open("la Placa Ya existe", "Aceptar", {
                      duration: 3000
                    });
                    this.loading = false;
                    this.formulario.delete("placa")
                  }
                });
            })
          )
          .subscribe();
      }
    else{
      this.snack.open("Faltan Datos", "Aceptar", {
        duration: 3000
      });
      this.formulario.delete("placa")
    }
      
  }

  getfiles(event, tipo: string) {
    if (tipo == "licencia") {
      this.formulario.append("licencia", <File>event.files[0], "licencia.pdf");
      this.vehiculo.licencia.url = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/licencia.pdf`;
    }
    if (tipo == "soat") {
      this.formulario.append("soat", <File>event.files[0], "soat.pdf");
      this.vehiculo.soat.url = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/soat.pdf`;
    }
    if (tipo == "tecnomecanica") {
      this.formulario.append(
        "tecnomecanica",
        <File>event.files[0],
        "tecnomecanica.pdf"
      );
      this.vehiculo.tecnomecanica.url = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/tecnomecanica.pdf`;
    }
    if (tipo == "residencia") {
      this.formulario.append(
        "residencia",
        <File>event.files[0],
        "residencia.pdf"
      );
      this.vehiculo.persona_naturar.urlcertificado = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/residencia.pdf`;
    }
    if (tipo == "cbancaria") {
      this.formulario.append(
        "cbancaria",
        <File>event.files[0],
        "certificacionbancaria.pdf"
      );
      this.vehiculo.persona_naturar.urlbanco = `http://localhost:3000/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/certificacionbancaria.pdf`;
    }
    if (tipo == "rut") {
      this.formulario.append("rut", <File>event.files[0], "rut.pdf");
      this.vehiculo.persona_naturar.urlrut = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/rut.pdf`;
    }
    if (tipo == "contrato") {
      this.formulario.append("contrato", <File>event.files[0], "contrato.pdf");
      this.vehiculo.persona_naturar.urlcontrato = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/contrato.pdf`;
    }
    if (tipo == "seguridad") {
      this.formulario.append(
        "seguridad",
        <File>event.files[0],
        "seguridad.pdf"
      );
      this.vehiculo.persona_naturar.urlseguridad = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/seguridad.pdf`;
    }
    if (tipo == "foto") {
      this.formulario.append("foto", <File>event.files[0], "foto3x4.pdf");
      this.vehiculo.persona_naturar.urlfoto = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/foto3x4.pdf`;
    }
    if (tipo == "camarac") {
      this.formulario.append("camarac", <File>event.files[0], "camarac.pdf");
      this.vehiculo.empresa.urlcc = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/camarac.pdf`;
    }
    if (tipo == "certificadobe") {
      this.formulario.append(
        "certificadobe",
        <File>event.files[0],
        "certificadobancario.pdf"
      );
      this.vehiculo.empresa.certificado = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/certificadobancario.pdf`;
    }
    if (tipo == "Poliza responsabilidad civil") {
      this.formulario.append(
        "resposabilidadcivil",
        <File>event.files[0],
        "Polizaresponsabilidadcivil.pdf"
      );
      this.vehiculo.poliza[0].url = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/Polizaresponsabilidadcivil.pdf`;
    }
    if (tipo == "Poliza Hidrocarburos") {
      this.formulario.append(
        "hidrocarburos",
        <File>event.files[0],
        "hidrocarburos.pdf"
      );
      this.vehiculo.poliza[1].url = `http://localhost:3000/asset/Documentos/Vehiculos/${
        this.vehiculo.placa
      }/hidrocarburos.pdf`;
    }
  }
  changetab(event: MatTabChangeEvent) {
    if (this.update) {
      if (event.index == 1)
        for (const key in this.vehiculo.persona_naturar) {
          delete this.vehiculo.persona_naturar[key];
        }
      if (event.index == 0)
        for (const key in this.vehiculo.empresa) {
          delete this.vehiculo.persona_naturar[key];
        }
    }
  }
}
