import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSnackBar} from '@angular/material';
import { User } from '../../../classes/usuario';
import { FormGroup, FormBuilder,Validators  } from '@angular/forms';

@Component({
  selector: 'app-updateuser',
  templateUrl: './updateuser.component.html',
  styleUrls: ['./updateuser.component.css']
})
export class UpdateuserComponent implements OnInit {
  forma:FormGroup
  datadia:User=this.data;
 
  constructor(
    public dialogRef: MatDialogRef<UpdateuserComponent>,
    @Inject(MAT_DIALOG_DATA) public data: User,public fb:FormBuilder,private snack: MatSnackBar,) {

    this.forma=fb.group({
     'password':["",Validators.required],
     'passwordc':["",Validators.required],
     'rol':["",Validators.required]

    })
    }

  update(formuser:FormGroup){
   console.log(this.forma);
   if(formuser.valid && this.forma.value.password==this.forma.value.passwordc){
      this.data.pwd=this.forma.value.password
      this.data.rol=this.forma.value.rol
      this.dialogRef.close(this.data)
   }else{
      
    this.snack.open("Contraseña no Coincide", "Aceptar", {
      duration: 3000,
      verticalPosition: "top"
    })
   }
  }
  ngOnInit() {
  }

}
