import { LicenciaTransito } from "./licenciaT";
import { Poliza } from "./polizas";
import { Soat } from "./soat";
import { Tecnomecanica } from "./tecnomecanica";
import { PersonaNaturar } from "./persona_naturar";
import { Empresa } from './empresa';

export class Vehiculo {
  public placa:string;
  public modelo:string;
  public marca:string;
  public linea:string;
  public observaciones:string;
  public licencia: LicenciaTransito;
  public poliza: Poliza[];
  public soat: Soat;
  public tecnomecanica: Tecnomecanica;
  public persona_naturar: PersonaNaturar;
  public empresa:Empresa;

  constructor(
    licencia: LicenciaTransito,
    poliza: Poliza[],
    soat: Soat,
    tecnomecanica: Tecnomecanica,
    personanaturar: PersonaNaturar,
    empresa:Empresa
  ) {
    this.poliza = poliza;
    this.licencia = licencia;
    this.soat = soat;
    this.tecnomecanica = tecnomecanica;
    this.persona_naturar = personanaturar;
    this.empresa=empresa
  }
}
