import { Component, OnInit, Input } from '@angular/core';
import { LicenciaTransito } from 'src/app/classes/vehiculo/licenciaT';
import { KingPing } from 'src/app/classes/trailes/kingping';
import { TrailersService } from 'src/app/services/trailers.service';
import { Trailer } from 'src/app/classes/trailes/trailer';
import { MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { FormGroup } from '@angular/forms';
import { finalize } from 'rxjs/operators';

@Component({
  selector: 'app-addcamabaja',
  templateUrl: './addcamabaja.component.html',
  styleUrls: ['./addcamabaja.component.css']
})
export class AddcamabajaComponent implements OnInit {
  
  @Input() update: boolean;
  formulario = new FormData();
  licenciat = new LicenciaTransito();
  kingping = new KingPing();
  camabaja = new Trailer(this.licenciat, this.kingping);
  constructor(
    public trailerservice: TrailersService,
    public snackBar: MatSnackBar,
    public nav:Router
  ) {
    this.camabaja.tipo = "camabaja";
  }
  ngOnInit() {


  }
  upload(form:FormGroup){

    this.formulario.append("placa", this.camabaja.placa);
    this.formulario.append("tipo", this.camabaja.tipo);
    if (form.valid) {
      if (this.update) {
        //actulizar
      } else {
        this.trailerservice
          .upload(this.formulario)
          .pipe(
            finalize(() => {
              this.trailerservice
                .addtrailer(this.camabaja)
                .subscribe((data: any) => {
                  if (data.error) {
                    this.snackBar.open("la Placa Ya existe", "Aceptar", {
                      duration: 3000
                    });
                  } else {
                    this.snackBar.open("Cama Alta Guardado", "Aceptar", {
                      duration: 3000
                    });
                    this.nav
                      .navigateByUrl("", { skipLocationChange: true })
                      .then(() => this.nav.navigate(["/Home/addcamabaja"]));
                  }
                });
            })
          )
          .subscribe();
      }
    } else {


    }

  }
  getfiles(event, tipo: string) {
    if (tipo == "permiso") {
      this.formulario.append("permiso", <File>event.files[0], "permiso.pdf");
      this.camabaja.urlpermiso = `http://localhost:3000/Documentos/Trailers/${this.camabaja.tipo}-${
        this.camabaja.placa
      }/permiso.pdf`;
    }
    if (tipo == "licencia") {
      this.formulario.append("licencia", <File>event.files[0], "licencia.pdf");
      this.camabaja.licencia.url = `http://localhost:3000/Documentos/Trailers/${this.camabaja.tipo}-${
        this.camabaja.placa
      }/licencia.pdf`;
    }
    if (tipo == "foto") {
      this.formulario.append("foto", <File>event.files[0], "foto.pdf");
      this.camabaja.foto = `http://localhost:3000/Documentos/Trailers/${this.camabaja.tipo}-${
        this.camabaja.placa
      }/foto.pdf`;
    }
    if (tipo == "aparejos") {
      this.formulario.append("aparejos", <File>event.files[0], "aparejos.pdf");
      this.camabaja.urlaparejos = `http://localhost:3000/Documentos/Trailers/${this.camabaja.tipo}-${
        this.camabaja.placa
      }/aparejos.pdf`;
    }
    if (tipo == "kingping") {
      this.formulario.append("kingping", <File>event.files[0], "kingping.pdf");
      this.camabaja.kinping.url = `http://localhost:3000/Documentos/Trailers/${this.camabaja.tipo}-${
        this.camabaja.placa
      }/kingping.pdf`;
    }
  }
}
