import { Component, OnInit, ViewChild } from "@angular/core";
import { User } from "../../classes/usuario";
import { AuthService } from "../../services/auth.service";
import { MatSnackBar, MatPaginator, MatDialog } from "@angular/material";
import { MatTableDataSource, MatSort } from "@angular/material";
import { UpdateuserComponent } from "./updateuser/updateuser.component";

@Component({
  selector: "app-usuario",
  templateUrl: "./usuario.component.html",
  styleUrls: ["./usuario.component.css"]
})
export class UsuarioComponent implements OnInit {
  deletespiner: boolean;
  comfirpwd: string;
  usuario = new User();
  displayedColumns: string[] = [
    "numero",
    "username",
    "rol",
    "Eliminar",
    "update"
  ];
  dataSource = new MatTableDataSource();
  constructor(
    public auth: AuthService,
    private snack: MatSnackBar,
    public dialog: MatDialog
  ) {}
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  ngOnInit() {
    this.deletespiner = true;
    this.paginator._intl.itemsPerPageLabel = "Item por pagina";
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.auth.get().subscribe((data: User[]) => {
      console.log(data);

      this.dataSource.data = data;
      this.deletespiner = false;
    });
  }
  upload(formuser) {
    console.log(formuser);

    if (formuser.valid && this.usuario.pwd == this.comfirpwd) {
      this.deletespiner = true;
      this.auth.sing(this.usuario).subscribe(
        (data: any) => {
          console.log(data);
          this.snack.open("Usuario Agregado", "Aceptar", {
            duration: 3000,
            verticalPosition: "top"
          });
          this.ngOnInit();
        },
        err => {
          console.log(err);
          if (err.status == 400)
            this.snack.open("Usuario Ya Existe", "Aceptar", {
              duration: 3000,
              verticalPosition: "top"
            });
          this.deletespiner = false;
        }
      );
    } else
      this.snack.open("Las Contraseña no Coinciden", "Aceptar", {
        duration: 3000,
        verticalPosition: "top"
      });
  }

  delete(username: string) {
    let comfirm = confirm("Esta seguro de Borrar el usuario");
    if (comfirm) {
      this.deletespiner = true;
      this.auth.delete(username).subscribe(data => {
        this.ngOnInit();
      });
    }
  }
  updateuser(username: string) {
    console.log(username);
    this.dataSource.data.forEach((user: User) => {
      if (user.username == username) {
        const dialogRef = this.dialog.open(UpdateuserComponent, {
          width: "400px",
          data: user
        });
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            user = result;
            this.auth.update(result).subscribe(data => {
              this.snack.open("Usuario Actulizado", "Aceptar", {
                duration: 3000,
                verticalPosition: "top"
              });
            });
          }
        });
      }
    });
  }
  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
}
