import { Component, OnInit, Input } from "@angular/core";
import { LicenciaTransito } from "../../classes/vehiculo/licenciaT";
import { KingPing } from "../../classes/trailes/kingping";
import { Trailer } from "../../classes/trailes/trailer";
import { FormGroup } from "@angular/forms";
import { TrailersService } from "../../services/trailers.service";
import { finalize } from "rxjs/operators";
import { MatSnackBar } from "@angular/material";
import { Router } from '@angular/router';

@Component({
  selector: "app-addcamaalta",
  templateUrl: "./addcamaalta.component.html",
  styleUrls: ["./addcamaalta.component.css"]
})
export class AddcamaaltaComponent implements OnInit {
  @Input() update: boolean;
  formulario = new FormData();
  licenciat = new LicenciaTransito();
  kingping = new KingPing();
  camaalta = new Trailer(this.licenciat, this.kingping);
  constructor(
    public trailerservice: TrailersService,
    public snackBar: MatSnackBar,
    public nav:Router
  ) {
    this.camaalta.tipo = "camaalta";
  }

  ngOnInit() {}

  upload(form: FormGroup) {
    this.formulario.append("placa", this.camaalta.placa);
    this.formulario.append("tipo", this.camaalta.tipo);
    if (form.valid) {
      if (this.update) {
        //actulizar
      } else {
        this.trailerservice
          .upload(this.formulario)
          .pipe(
            finalize(() => {
              this.trailerservice
                .addtrailer(this.camaalta)
                .subscribe((data: any) => {
                  if (data.error) {
                    this.snackBar.open("la Placa Ya existe", "Aceptar", {
                      duration: 3000
                    });
                  } else {
                    this.snackBar.open("Cama Alta Guardado", "Aceptar", {
                      duration: 3000
                    });
                    this.nav
                      .navigateByUrl("", { skipLocationChange: true })
                      .then(() => this.nav.navigate(["/Home/trailer"]));
                  }
                });
            })
          )
          .subscribe();
      }
    } else {
    }
  }

  getfiles(event, tipo: string) {
    if (tipo == "licencia") {
      this.formulario.append("licencia", <File>event.files[0], "licencia.pdf");
      this.camaalta.licencia.url = `http://localhost:3000/Documentos/Trailers/${this.camaalta.tipo}-${
        this.camaalta.placa
      }/licencia.pdf`;
    }
    if (tipo == "foto") {
      this.formulario.append("foto", <File>event.files[0], "foto.pdf");
      this.camaalta.foto = `http://localhost:3000/Documentos/Trailers/${this.camaalta.tipo}-${
        this.camaalta.placa
      }/foto.pdf`;
    }
    if (tipo == "cadenas") {
      this.formulario.append("cadenas", <File>event.files[0], "cadenas.pdf");
      this.camaalta.urlcadenas = `http://localhost:3000/Documentos/Trailers/${this.camaalta.tipo}-${
        this.camaalta.placa
      }/cadenas.pdf`;
    }
    if (tipo == "kingping") {
      this.formulario.append("kingping", <File>event.files[0], "kingping.pdf");
      this.camaalta.kinping.url = `http://localhost:3000/Documentos/Trailers/${this.camaalta.tipo}-${
        this.camaalta.placa
      }/kingping.pdf`;
    }
  }
}
