import { Component, OnInit, ViewChild } from "@angular/core";
import {
  MatTableDataSource,
  MatDialog,
  MatSnackBar,
  MatSort,
  MatPaginator
} from "@angular/material";
import { TrailersService } from "../../services/trailers.service";
import { Router } from "@angular/router";
import { Trailer } from "../../classes/trailes/trailer";
import { finalize } from "rxjs/operators";
import { DetallestrailerComponent } from './detallestrailer/detallestrailer.component';

@Component({
  selector: "app-addtrailer",
  templateUrl: "./trailer.component.html",
  styleUrls: ["./trailer.component.css"]
})
export class TrailerComponent implements OnInit {

  @ViewChild(MatSort) sort: MatSort;
 @ViewChild(MatPaginator) paginas:MatPaginator
  uploadbar: boolean;
  validodisable: boolean;

  // Tabla de Tanques

  displayedColumnstanque: string[] = [
    "datos",
    "licencia",
    "hidrostatica",
    "aforos",
    "lineas",
    "kingping",
    "editar",
    "eliminar",
    "detalles",
    "validar"
  ];
  dataSourcetanque = new MatTableDataSource();

  constructor(
    public trailerservice: TrailersService,
    private snackBar: MatSnackBar,
    public dialog: MatDialog,
    public router: Router
  ) {}

  ngOnInit() {
    this.dataSourcetanque.paginator=this.paginas
    this.paginas._intl.itemsPerPageLabel=""
   
    this.dataSourcetanque.sort = this.sort;
    this.trailerservice
      .get("tanque")
      .pipe(finalize(() => (this.uploadbar = false)))
      .subscribe((data: Trailer[]) => {
        console.log(data);
        this.dataSourcetanque.data = data;
      });
  }
  upload(form) {}

  deleteonductor(id) {
    this.uploadbar = true;
    let comfirm = confirm("Desea Eliminar el Trailer");
    if (comfirm) {
      this.trailerservice.delete(id).subscribe((data: any) => {
        this.ngOnInit();
      });
    } else this.uploadbar = false;
  }
  viewdetalles(trailer){
    this.dialog.open(DetallestrailerComponent,{
      width: "1000px",
      data:trailer
    })
  }
  validar(id){
    
  }
}

