import { Licencia } from './licencia';
import { Seguridad } from './seguridad_social';
import { Vacunas } from './vacunas';
import { Cursos } from './cursos';
export class Conductor {
public nombre: string;
public cedula:string;
public urlcedula:string;
public edad:string;
public email:string;
public direccion:string;
public telefono:string;
public eps:string;
public arl:string;
public licencia: Licencia;
public comparendos:boolean;
public cursos: Cursos[];
public fotografia: string;
public urlfoto:string;
public seguridad: Seguridad;
public vacunas:Vacunas;
public urlsimit:string;
public urlrunt:string;

constructor(licencia:Licencia,seguridad:Seguridad,vacunas:Vacunas,cursos:Cursos[]){
   
    this.licencia=licencia
    this.seguridad=seguridad
    this.vacunas=vacunas
    this.cursos=cursos
}

}
