import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { User } from "../classes/usuario";
import { Router } from '@angular/router';
import { MatSnackBar } from "@angular/material";

@Injectable({
  providedIn: "root"
})
export class AuthService {
 
  constructor(public http: HttpClient,public router:Router, private snack: MatSnackBar) {}

  auth(usuario: any) {
    return this.http.post("http://167.99.154.108:3000/usuario/login", usuario);
  }

  sing(usuario: User) {
    return this.http.post("http://167.99.154.108:3000/usuario/new", usuario);
  }
  get() {
    return this.http.get("http://167.99.154.108:3000/usuario/get");
  }

  update(usuario) {
    return this.http.put("http://167.99.154.108:3000/usuario/update", usuario);
  }
  loginout(){
      this.snack.open("Caduco la Seccion Ingrese de nuevo", "Aceptar", {
      verticalPosition: "top"
    });
    localStorage.removeItem('token')
    this.router.navigateByUrl("/login")
  }
  delete(usuario){

    return this.http.delete(`http://167.99.154.108:3000/usuario/delete/${usuario}`)
  }
}
