import { Component, OnInit, ViewChild } from "@angular/core";
import {
  MatSnackBar,
  MatPaginator,
  MatDialog,
  MatSort,
  MatTableDataSource,
  MatCheckboxChange
} from "@angular/material";
import { Conductor } from "../../classes/conductor/conductor";
import { Licencia } from "../../classes/conductor/licencia";
import { Seguridad } from "../../classes/conductor/seguridad_social";
import { Vacunas } from "../../classes/conductor/vacunas";
import { Cursos } from "../../classes/conductor/cursos";
import { ConductorService } from "src/app/services/conductor.service";
import { finalize } from "rxjs/operators";
import { CursosComponent } from "./cursos/cursos.component";
import { Router } from "@angular/router";
import { DetallesconductorComponent } from './detallesconductor/detallesconductor.component';

@Component({
  selector: "app-conductores",
  templateUrl: "./conductores.component.html",
  styleUrls: ["./conductores.component.css"]
})
export class ConductoresComponent implements OnInit {
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  uploadbar: boolean;
  validodisable:boolean;
  displayedColumns: string[] = [
    "datos",
    "vacunas",
    "seguridadsocial",
    "comparendos",
    "licencia",
    "cursos",
    "editar",
    "eliminar",
    "detalles",
    "validar"
  ];
  dataSource = new MatTableDataSource();
  constructor(
    public _cnductorservice: ConductorService,
    private snackBar: MatSnackBar,
    public dialog: MatDialog,
    public router: Router
  ) {}

  ngOnInit() {
    this.uploadbar = true;
    this.paginator._intl.itemsPerPageLabel = "Item por pagina";
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this._cnductorservice
      .getconductor()
      .pipe(finalize(() => this.uploadbar = false))
      .subscribe((data: Conductor[]) => {
        console.log(data);

        this.dataSource.data = data;
        this.validodisable=false;
      });
  }

  viewcursos(cursos: Cursos) {
    const dialogref = this.dialog.open(CursosComponent, {
      width: "1000px",
      data: cursos
    });
  }

  updateconductor() {}
  deleteonductor(conductor) {
    let comfirm = confirm("Desea Eliminar el Conductor");
    if (comfirm) {
      this.uploadbar = true;
      this._cnductorservice.deleteconductor(conductor).subscribe(data => {
        if (data) {
          this.snackBar.open("Conductor Eliminado", "Aceptar", {
            duration: 3000
          });
          this.ngOnInit();
        }
      });
    }
  }
  validar(event: MatCheckboxChange, conductor: string) {
    this.validodisable=true;
    let valido = {
      _id: conductor,
      valid: event.checked
    };
    this._cnductorservice.validar(valido).subscribe((data: any) => {
      
      if (!data.error) {
        this.snackBar.open("Conductor Validado", "Aceptar", {
          duration: 3000
        });
       this.ngOnInit()
      } else {
        this.snackBar.open("Error al Validar", "Aceptar", {
          duration: 3000
        });
      }
    });
  }
  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  viewdetalles(conductor){
    const dialogref = this.dialog.open(DetallesconductorComponent, {
      width: "1000px",
      data: conductor
    });
  }
}
