import { Injectable } from "@angular/core";
import { Vehiculo } from "../classes/vehiculo/vehiculo";
import { HttpClient,HttpHeaders } from "@angular/common/http";

@Injectable({
  providedIn: "root"
})
export class VehiculoService {
  constructor(public http: HttpClient) {}

  addvehiculo(vehiculo: Vehiculo) {
    return this.http.post("http://167.99.154.108:3000/vehiculo/new", vehiculo);
  }

  uploadfiles(files: FormData) {
    return this.http.post("http://167.99.154.108:3000/vehiculo/upload", files);
  }
  get(){
    return this.http.get("http://167.99.154.108:3000/vehiculo/get")
  }
  delete(id){
   return this.http.delete(`http://167.99.154.108:3000/vehiculo/delete/${id}`)

  }
  getvehiculo(placa){
   return this.http.get(`http://167.99.154.108:3000/vehiculo/get/${placa}`)
  }
  update(vehiculo){
    return this.http.put("http://167.99.154.108:3000/vehiculo/update",vehiculo)
  }
  
  updatefiles(files:FormData){
    return this.http.post("http://167.99.154.108:3000/vehiculo/upload/update",files)
  }
  validar(vehiculo){
      return this.http.put("http://167.99.154.108:3000/vehiculo/validar",vehiculo)
  }
}
