import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Conductor } from '../classes/conductor/conductor';
@Injectable({
  providedIn: "root"
})
export class ConductorService {
  constructor(public http: HttpClient) {}

  addconductorfiles(form: FormData) {
    return this.http.post("http://167.99.154.108:3000/conductor/upload", form);
  }
  addconductor(conductor: Conductor) {
    return this.http.post("http://167.99.154.108:3000/conductor/new", conductor);
  }
  getconductor() {
    return this.http.get("http://167.99.154.108:3000/conductor/get");
  }
  get(cedula) {
    return this.http.get(`http://167.99.154.108:3000/conductor/get/${cedula}`);
  }
  deleteconductor(conductor) {
    return this.http.delete(
      `http://167.99.154.108:3000/conductor/delete/${conductor}`
    );
  }
  update(conductor:Conductor){
    return this.http.put("http://167.99.154.108:3000/conductor/update",conductor)
  }
  addconductorfilesupdate(form:FormData){
    return this.http.post("http://167.99.154.108:3000/conductor/upload/update", form);
  }
  validar(valido){
    return this.http.put("http://167.99.154.108:3000/conductor/validar",valido)
  }
}
