import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-updatetanque',
  templateUrl: './updatetanque.component.html',
  styleUrls: ['./updatetanque.component.css']
})
export class UpdatetanqueComponent implements OnInit {
  placaupdate: string;
  update:boolean;
  constructor(public route:ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(param => {
      this.placaupdate = param.placa;
      this.update=true;
    });
  }

}
