import { Component, OnInit, ViewChild } from '@angular/core';
import { MatSort,MatTableDataSource, MatTable} from '@angular/material';
import { ContratoService } from '../../services/contrato.service';
import { Conductor } from '../../classes/conductor/conductor';
import { CdkDragDrop,moveItemInArray,transferArrayItem } from '@angular/cdk/drag-drop';



@Component({
  selector: 'app-addcontrato',
  templateUrl: './addcontrato.component.html',
  styleUrls: ['./addcontrato.component.css']
})
export class AddcontratoComponent implements OnInit {
  displayedColumns: string[] = ['nombre', 'cedula'];
  dataSource = new MatTableDataSource();
  @ViewChild('table') table: MatTable<any>;
  @ViewChild(MatSort) sort: MatSort;
  constructor(public contratoser:ContratoService) { }
  
  ngOnInit() {
    this.dataSource.sort = this.sort;
    this.contratoser.getvalidconductor().subscribe((data:Conductor[])=>{
    console.log(data);
    
    this.dataSource.data=data

    })
  }
  drop(event: CdkDragDrop<Conductor[]>) {
    const prevIndex = this.dataSource.data.findIndex((d) => d === event.item.data);
    moveItemInArray(this.dataSource.data, prevIndex, event.currentIndex);
    this.table.renderRows();
  }
}
