import { Component, OnInit, Input } from "@angular/core";
import { Conductor } from "../../classes/conductor/conductor";
import { Licencia } from "../../classes/conductor/licencia";
import { Seguridad } from "../../classes/conductor/seguridad_social";
import { Vacunas } from "../../classes/conductor/vacunas";
import { Cursos } from "../../classes/conductor/cursos";
import { ConductorService } from "src/app/services/conductor.service";
import { MatSnackBar } from "@angular/material";
import { Router } from "@angular/router";
import { finalize } from "rxjs/operators";

@Component({
  selector: "app-addconductor",
  templateUrl: "./addconductor.component.html",
  styleUrls: ["./addconductor.component.css"]
})
export class AddconductorComponent implements OnInit {
  @Input() cedulaupdate: string;
  @Input() update: boolean = false;

  disableupload: boolean = true;
  
  formulario = new FormData();
  numfile: number = 0;
  tipofile: string;
  fechacurso:string="fecha"
  loading:boolean;

  // ----- Datos ---------
  licencia = new Licencia();
  seguridad = new Seguridad();
  vacunas = new Vacunas();
  cursos: Cursos[] = [
    new Cursos("Mecanica Basica"),
    new Cursos("Primeros auxilios"),
    new Cursos("Alturas Avanzado"),
    new Cursos("Normas Y Seguridad Vial"),
    new Cursos("Manejo de Extintor"),
    new Cursos("Manejo Defensivo")
  ];
  conductor = new Conductor(
    this.licencia,
    this.seguridad,
    this.vacunas,
    this.cursos
  );

  constructor(
    public _cnductorservice: ConductorService,
    private snackBar: MatSnackBar,
    public nav: Router
  ) {}

  
  ngOnInit() {
    console.log(this.cedulaupdate, this.update);
    if (this.update){
      this.disableupload=false
      this._cnductorservice
        .get(this.cedulaupdate)
        .subscribe((data: Conductor) => {
          this.conductor = data[0];
        });
    }
  
  }

  upload(form) {
    
    console.log(this.conductor);

    this.formulario.append("cedulaname", this.conductor.cedula);
    if (form.valid) {
     this.loading=true
      if (!this.update)
        this._cnductorservice
          .addconductorfiles(this.formulario)
          .pipe(
            finalize(() => {
              this._cnductorservice
                .addconductor(this.conductor)
                .subscribe((data: any) => {
                  if (data.status) {
                    this.snackBar.open("Conductor Guardado", "Aceptar", {
                      duration: 3000
                    });

                    this.nav
                      .navigateByUrl("", { skipLocationChange: true })
                      .then(() => this.nav.navigate(["/Home/addconductor"]));
                  }
                  if (data.error) {
                    this.snackBar.open("La cedula Ya existe", "Aceptar", {
                      duration: 3000
                    });
                    this.formulario.delete("cedulaname");
                  }
                });
            })
          )
          .subscribe();
      else {
        this._cnductorservice
          .addconductorfilesupdate(this.formulario)
          .pipe(
            finalize(() => {
              this._cnductorservice
                .update(this.conductor)
                .subscribe((data: any) => {
                  if (!data.error) {
                    this.snackBar.open("Conductor Actulizado", "Aceptar", {
                      duration: 3000
                    });
                    this.nav.navigate(["/Home/conductor"]);
                  }
                });
            })
          )
          .subscribe();
       
      }
    } else {
      this.snackBar.open(
        `Faltan Datos o Faltan Archivos por Subir`,
        "Aceptar",
        {
          duration: 3000
        }
      );
      this.formulario.delete("cedulaname");
    }

   
  }

  getfiles(event, tipo: string) {
    console.log(tipo);
    
    if (tipo == "cedula") {
      this.formulario.append("cedula", <File>event.files[0], "cedula.pdf");
      this.conductor.urlcedula = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/cedula.pdf`;
    }
    if (tipo == "foto3x4") {
      this.formulario.append("foto", <File>event.files[0], "foto3x4.pdf");
      this.conductor.urlfoto = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/foto.pdf`;
    }
    if (tipo == "vacunas") {
      this.formulario.append("vacunas", <File>event.files[0], "vacunas.pdf");
      this.vacunas.url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/vacunas.pdf`;
    }
    if (tipo == "plantilla") {
      this.formulario.append(
        "plantilla",
        <File>event.files[0],
        "plantilla.pdf"
      );
      this.seguridad.url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/plantilla.pdf`;
    }
    if (tipo == "simit") {
      this.formulario.append("simit", <File>event.files[0], "simit.pdf");
      this.conductor.urlsimit = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/simit.pdf`;
    }
    if (tipo == "runt") {
      this.formulario.append("runt", <File>event.files[0], "runt.pdf");
      this.conductor.urlrunt = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/runt.pdf`;
    }
    if (tipo == "licenciaC") {
      this.formulario.append("licencia", <File>event.files[0], "licencia.pdf");
      this.licencia.url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/licencia.pdf`;
    }
    if (tipo == "Mecanica Basica") {
      this.formulario.append(
        "mecanicabasica",
        <File>event.files[0],
        "mecanicabasica.pdf"
      );
      this.conductor.cursos[0].url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/MecanicaBasica.pdf`;
    }
    if (tipo == "Primeros auxilios") {
      this.formulario.append(
        "primerosauxilios",
        <File>event.files[0],
        "primeroauxilios.pdf"
      );
      this.conductor.cursos[1].url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/PrimerosAuxilios.pdf`;
    }
    if (tipo == "Alturas Avanzado") {
      
      this.formulario.append(
        "alturaavanzado",
        <File>event.files[0],
        "alturasavanzado.pdf"
      );
      this.conductor.cursos[2].url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/AlturasAvanzado.pdf`;
      console.log(this.cursos[2].url);
      
    }
    if (tipo == "Normas Y Seguridad Vial") {
      this.formulario.append(
        "seguridadvial",
        <File>event.files[0],
        "seguridadvial.pdf"
      );
      this.conductor.cursos[3].url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/NormasySeguridad.pdf`;
    }
    if (tipo == "Manejo de Extintor") {
      this.formulario.append(
        "manejoextintor",
        <File>event.files[0],
        "manejoextintor.pdf"
      );
      this.conductor.cursos[4].url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/ManejoDeextintor.pdf`;
    }
    if (tipo == "Manejo Defensivo") {
      this.formulario.append(
        "manejodefensivo",
        <File>event.files[0],
        "manejodefensivo.pdf"
      );
      this.conductor.cursos[5].url = `http://localhost:3000/asset/Documentos/Conductores/${
        this.conductor.cedula
      }/ManejoDeextintor.pdf`;
    }
    this.numfile++;
  }

  disablefiles() {
    if (this.conductor.cedula == undefined || this.conductor.cedula == "") {
      this.disableupload = true;
    } else this.disableupload = false;
  }
  statusfile(tipo: string) {
    this.formulario.delete(tipo);
    this.tipofile = tipo;
    this.numfile--;
  }
}
